import React, { useEffect } from "react";
import { useParams } from 'react-router-dom';
import { useSelector, useDispatch } from "react-redux";
import { setUser } from "./store";

const UserInfo = () => {
  const userList = useSelector((state) => state.userList.users)

  const dispatch = useDispatch()
  const actionSetUser = (users) => dispatch(setUser(users))
  const { id } = useParams();

  const currentUserInfo = userList.filter( (item) => item.id === Number(id))
  const userInfo = useSelector((state) => state.userList.user) || currentUserInfo[0]
  console.log(currentUserInfo);

  useEffect(() => {
    if (id && !currentUserInfo.length) {
      fetch(`https://jsonplaceholder.typicode.com/users/${id}`)
      .then( (user) => {
        return user.json()
      })
      .then( (user) => {
        actionSetUser(user)
      })
      .catch( () => {
        actionSetUser(null)
      })
    }
  }, [])

  return  <React.Fragment>
            <h2>User Info</h2>

            {userInfo && 
              <ul className="list-group">
                <li className="list-group-item">name: {userInfo.name}</li>
                <li className="list-group-item">userName: {userInfo.username}</li>
                <li className="list-group-item">address: {userInfo.address.city}, {userInfo.address.street}</li>
                <li className="list-group-item">company: {userInfo.company.name}</li>
              </ul>
            }
            {userInfo === null &&  <div>Request Error</div>}
          </React.Fragment>;
}

export default UserInfo;