import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { setUsers } from "./store";

const UserList = () => {
  const userList = useSelector((state) => state.userList.users)
  const dispatch = useDispatch()
  const actionSetUsers = (users) => dispatch(setUsers(users))

  useEffect(() => {
    fetch(`https://jsonplaceholder.typicode.com/users`)
    .then( (users) => {
      return users.json()
    })
    .then( (users) => {
      actionSetUsers(users)
    })
    .catch( () => {
      actionSetUsers(null)
    })
  }, [])

  return  <React.Fragment>
            <h2>Users</h2>

            { userList &&
              <table className="table">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Email</th>
                  </tr>
                </thead>
                <tbody>
                { userList.map((item) => (
                  <tr key={item.id}>
                    <td>
                      <Link to={`/user/${item.id}`}>
                        {item.name}
                      </Link>
                    </td>
                    <td>{item.email}</td>
                  </tr>
                ))}
                </tbody>
              </table>
            }
            {userList === null &&  <div>Request Error</div>}
          </React.Fragment>
  
}
export default UserList;